.container {
  max-width: 300px;
  margin: 0 auto;
  padding: 20px;
}

.input {
  width: 300px;
  height: 36px;
  padding: 8px 10px;
  border-radius: 12px;
  border: 1px solid #e3ffa8;
  gap: 10px;
  background-color: #0f0f0f;
  color: #b6b6b6;
  margin-bottom: 16px;
}

.button {
  width: 300px;
  height: 36px;
  padding: 8px 10px 8px 10px;
  border-radius: 12px;
  gap: 10px;
  background-color: #e3ffa8;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: center;
  color: #0f0f0f;
  margin-bottom: 80px;
}

.error {
  color: rgb(0, 0, 0);
}

.imgIlistration {
  margin-bottom: 24px;
}

.heading {
  font-family: Poppins;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: left;
  margin-bottom: 16px;
}

.subheading {
  font-family: Poppins;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: left;
  color: #b6b6b6;
  margin-bottom: 24px;
}

.linkText {
  font-family: Poppins;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: left;
  color: #b6b6b6;
  margin-bottom: 16px;
}

.link {
  font-family: Poppins;
  font-size: 16px;
  font-weight: 800;
  line-height: 1.5;
  letter-spacing: 0em;
  text-align: left;
  width: 122px;
  height: 20px;
}

@media (min-width: 768px) and (max-width: 1099px) {
  .container {
    max-width: 834px;
    padding: 195px;
    padding-top: 40px;
  }
  .imgIlistration {
    display: flex;
    width: 380px;
    margin-bottom: 60px;
    align-items: stretch;
    justify-content: center;
  }

  .heading {
    font-family: Poppins;
    font-size: 30px;
    line-height: 1.3;
    text-align: center;
  }
  .subheading {
    width: 444px;
    font-size: 22px;
    line-height: 1.3;
    text-align: center;
  }

  .input {
    width: 380px;
    height: 36px;
    gap: 16px;
  }
  .button {
    width: 380px;
    margin-bottom: 238px;
  }
  .center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .linkContainer {
    display: flex;
    align-items: baseline;
    justify-content: center;
  }
  .linkText {
    font-size: 14px;
    margin-right: 16px;
  }
}

@media (min-width: 1100px) {
  .container {
    max-width: 1440px;
    padding-left: 150px;
    padding-top: 40px;
    display: flex;
  }
  .imgIlistration {
    display: block;
    max-width: 100%;
    height: auto;
    width: 592px;
    margin-right: 104px;
  }

  .center {
    display: flex;
    flex-direction: column;
  }
  .input {
    width: 212px;
  }
  .button {
    width: 212px;
    margin-bottom: 200px;
  }
  .linkContainer {
    display: flex;
    align-items: baseline;
    justify-content: center;
  }
  .linkText {
    font-size: 12px;
    margin-right: 12px;
  }
  .link {
    font-size: 14px;
  }
}
