.container {
  max-width: 300px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 5px;
}

.input {
  width: 300px;
  height: 36px;
  padding: 8px 10px;
  border-radius: 12px;
  border: 1px solid #e3ffa8;
  gap: 10px;
  background-color: #0f0f0f;
  color: #b6b6b6;
  margin-bottom: 16px;
}

.button {
  width: 300px;
  height: 36px;
  padding: 8px 10px 8px 10px;
  border-radius: 12px;
  gap: 10px;
  background-color: #e3ffa8;
  font-family: Poppins;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: center;
  color: #0f0f0f;
  margin-bottom: 80px;
}
.error {
  color: rgb(20, 9, 9);
}

.imgContainer {
  margin-bottom: 24px;
}

.heading {
  font-family: Poppins;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
  margin-bottom: 16px;
}

.h3 {
  font-family: Poppins;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0em;
  text-align: left;
  color: #b6b6b6;
  width: 300px;
  margin-bottom: 24px;
}

.account {
  font-family: Poppins;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0em;
  text-align: left;
  color: #b6b6b6;
  margin-bottom: 16px;
}

.signInLink {
  font-family: Poppins;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 0em;
  text-align: left;
  color: #ffffff;
}
